<template>
  <div class="dialog" v-show="visable">
    <div>
      <v-card width="500px">
        <v-card-title>
          <span class="headline"></span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="10">
                  <v-text-field
                    label=""
                    persistent-hint
                    v-model="formData.usrName"
                    :rules="[v => !!v || '']"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="10">
                  <v-text-field
                    label=""
                    v-model="formData.phoneNum"
                    :rules="[
                      v => !!v || '',
                      v => /^1[3456789]\d{9}$/.test(v) || ''
                    ]"
                  ></v-text-field>
                </v-col>
                <v-col cols="10">
                  <v-text-field
                    label=""
                    v-model="formData.email"
                    :rules="[
                      v => !!v || '',
                      v => /.+@.+\..+/.test(v) || ''
                    ]"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="10">
                  <v-text-field
                    label=""
                    v-model="formData.addr"
                    :rules="[v => !!v || '']"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="10">
                  <v-text-field label="" required v-model="formData.remark"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="close"></v-btn>
          <v-btn text @click="submit"></v-btn>
        </v-card-actions>
      </v-card>
    </div>
    <v-snackbar v-model="snackbar" color="success" top :timeout="3000">{{ text }}</v-snackbar>
  </div>
</template>
<script>
export default {
  props: {
    visable: {
      required: true
    }
  },
  data() {
    return {
      valid: false,
      formData: {
        usrName: "",
        phoneNum: "",
        email: "",
        addr: "",
        remark: ""
      },
      snackbar: false,
      text: ""
    };
  },
  methods: {
    submit() {
      if (this.$refs.form.validate()) {
        //
        this.$axios.post("/api/applyInfo/add", this.formData).then(res => {
          this.close();
        });
      } else {
        this.snackbar = true;
      }
    },
    close() {
      this.$emit("changeDialog");
      this.formData = {
        usrName: "",
        phoneNum: "",
        email: "",
        addr: "",
        remark: ""
      };
    }
  }
};
</script>
<style lang="less">
.dialog {
  width: 100%;
  top: 0;
  position: fixed;
  z-index: 999;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  .v-card {
    margin: 50px auto;
  }
  .v-messages__message {
    color: red;
  }
}
</style>
